.home {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    &__text-wrapper {
        display: flex;
        justify-content: center;

        h1 {
            color: var(--yellow-theme-main-color);
            margin: 0px;
            font-weight: 500;
            font-size: 6rem;
            line-height: 8rem;
            position: relative;
            overflow: hidden;
            white-space: nowrap;
            width: 0;
            border-right: 2px solid var(--yellow-theme-main-color);
            animation:
                typingEffectAnimation 2.5s steps(30, end) forwards,
                removeCursor 0.1s ease 2.6s forwards; // runs after typing ends
        }
    }

    &_contact-me {
        display: flex;
        justify-content: center;
        margin-top: 15px;

        button {
            cursor: pointer;
            padding: 10px 70px;
            background: transparent;
            color: var(--yellow-theme-main-color);
            border: 1.5px solid var(--yellow-theme-main-color);
            border-radius: 4px;
            font-weight: 500;

            &:hover {
                background: var(--yellow-theme-main-color);
                color: #000000;
            }
        }
    }
}

@media only screen and (max-width:550px) {
    .home__text-wrapper h1 {
        font-size: 2rem;
        line-height: 4rem;
    }
}


@media only screen and (min-width:551px) and (max-width:768px) {
    .home__text-wrapper h1 {
        font-size: 3rem;
        line-height: 5rem;
    }
}

@media only screen and (min-width:769px) and (max-width:990px) {
    .home__text-wrapper h1 {
        font-size: 3rem;
        line-height: 5rem;
    }
}

@keyframes typingEffectAnimation {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

@keyframes removeCursor {
    to {
        border-right: none;
    }
}